a.navbar-brand {
  white-space: normal;
  text-align: center;
  word-break: break-all;
}

html {
  font-size: 14px;
}
@media (min-width: 768px) {
  html {
    font-size: 16px;
  }
}

.box-shadow {
  box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.05);
}

.header {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 1000;
  background-color: #fff;
}

.flex-center {
  justify-content: center;
}

.none-list-style {
  list-style-type: none;
}

.trim-text {
  padding: 0;
}

.navbar {
  border-bottom: 1px solid #d0011b;
}

.navigation {
  min-width: 60%;
  display: flex;
  align-items: center;
}

.nav-item {
  padding: 0 8px;
  position: relative;
  transition: transform 0.1s cubic-bezier(0.4, 0, 0.6, 1),
    box-shadow 0.1s cubic-bezier(0.4, 0, 0.6, 1);
  top: 0px;
}

.nav-item:hover {
  opacity: 0.8;
}

@media screen and (max-width: 768px) and (min-width: 576px) {
  .sub-nav {
    display: none;
    margin: 0;
    padding: 0;
  }
}

@media screen and (max-width: 576px) {
  .nav-item {
    font-size: 24px;
    width: 100%;
    font-weight: 500;
    text-align: center;
    letter-spacing: 0.8px;
    opacity: 0.8;
    border-radius: 4px;
  }
  .nav-item:hover {
    background: rgb(236, 236, 236);
  }
}

@media screen and (width: 576px) {
  .nav-item {
    font-size: 14px;
  }
}
